body {
  align-items: center;
  display: flex;
  flex-direction: column;
  font-family: "Apple Garamond", "Baskerville", "Times New Roman", "Droid Serif",
    "Times", "Source Serif Pro", serif;
  justify-content: space-between;
  margin: 0;
}

h1 {
  text-align: center;
}

.info {
  align-items: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-top: 30px;
}

.land {
  stroke: #757575;
  fill: #e8f5e9;
}

.water {
  fill: #e3f2fd;
}

.d3-tip {
  background: #212121;
  border-radius: 20px;
  color: #f5f5f5;
  font-size: 1.5em;
  line-height: 1;
  padding: 9px;
}

.location {
  fill: #f44336;
  fill-opacity: 0.4;
}

.location:hover {
  stroke: #fff;
  stroke-width: 1px;
  fill-opacity: 0.9;
}
